
import {mapState,mapMutations} from "vuex";
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

export default {
name:"role",
computed:{
...mapState({
roleList: state => state.common.role.roleList,
vm: state => state.common.role.vm,
roleInfo: state => state.common.role.roleInfo,
menuList: state => state.common.role.menuList
})
},
components: {
XHeader,
Popup,
PopupHeader,
Checklist
},
created () {
this.query();
},
methods: {
...mapMutations({
query:'common/role/getList',
setCheck:'common/role/setCheck',
saveAuthority:'common/role/saveAuthority'
}),
gotoForm(item){
this.$store.commit('common/role/clear');
if(item){
this.$router.push({path:"/system/role/form",query:{roleId:item.id}});
}else{
this.$router.push({path:"/system/role/form"});
}
},
handleDelete(roleId) {
const _this = this;
this.$vux.confirm.show({
title:"删除提示",
content:"确定要删除该条记录?",
onCancel () {
_this.$vux.toast.text('已取消删除', 'top');
},
onConfirm () {
_this.$store.commit({
type: 'common/role/delete',
id: roleId
})
}
})
},
getAuthority(item){
this.$store.commit({
type:"common/role/getAuthority",
roleInfo:item
})
}
}
}



